import React from 'react';
import styles from './index.css';
import { Store, ModalStore } from '../../store';
import { useModalOpen } from '../hooks';

const Awards = () => {
    const store = Store.useContainer();
    const { states, changeStates, toAwardList } = store;
    const { award, isGrounds } = states;
    const { prizename, img, iscoin } = award;
    const modalStore = ModalStore.useContainer();
    const { changeModalStates } = modalStore;

    const reset = () => {
        changeStates({ isPlaying: false, award: null, current: -1 });
    };

    const handleClose = () => {
        changeModalStates({ awards: false });
        reset();
    };

    const handleGetAwardList = () => {
        handleClose();
        toAwardList();
    
    };

    const handleCoin = () => {
        handleClose();
    };

    const handlePreventClose = e => {
        e.stopPropagation();
    };

    useModalOpen();

    return (
        <div className={styles.mask} onClick={handleClose}>
            <div className={styles.modal} onClick={handlePreventClose}>
            <div className={styles.text}>{prizename}</div>
                <div className={styles.content}>
                    <div className={styles.light} />
                    <div className={styles.thumbnail}>
                        <img src={img} alt="" />
                    </div>
                    
                </div>
                {/* {iscoin === 1 ? (
                    <div className={`${styles.button} ${styles.buttonCoin}`} onClick={handleCoin} />
                ) : (
                    <div className={styles.button} onClick={handleGetAwardList} />
                )} */}
                
                <div className={styles.button} onClick={handleGetAwardList} />
                <div className={styles.closeBtn} onClick={handleClose} />
            </div>
        </div>
    );
};

export default Awards;
