import { useState } from 'react';
import { createContainer } from 'unstated-next';
import { getSearchParam } from '../utils/index';

/* eslint-disable */
const getBaseUrl = (env) => {
    switch (env) {
        case 'development':
            return `${window.location.origin}/mobile/news/loc/redEnvelopesLottery/priceList`;
        case 'staging':
            return 'http://test.news.ifeng.com/loc/redEnvelopesLottery/priceList';
        case 'production':
            // return 'http://news.ifeng.com';
            return `${window.location.origin}/redEnvelopesLottery/mobile_priceList.html`;
        default:
            break;
    }
};
// webpack
const baseUrl = getBaseUrl(NODE_ENV);

const useStore = (
    initialState = {
        isGrounds: false, // 
        isLogin: false, // 
        lotteryType: 'normal', // 
        coin: 0, // 
        ndrawtime: 0, // 
        hdrawtime: 0, // 
        drawtime: 0, // 
        version: 0, // 
        normalList: [], // 
        seniorList: [], // 
        removeList: [], // 
        award: {}, // 
        current: -1, // 
        winningList: [
            {
                username: '',
                typename: '',
                prizename: '',
            },
            {
                username: '',
                typename: '',
                prizename: '',
            },
            {
                username: '',
                typename: '',
                prizename: '20',
            },
            {
                username: '1',
                typename: '',
                prizename: '',
            },
            {
                username: '1',
                typename: '',
                prizename: '',
            },
            {
                username: '1',
                typename: '',
                prizename: '20',
            },
        ],
        signlist: [], // 
        signdays: 0, // 
    },
) => {
    const [states, setStates] = useState(initialState);

    const changeStates = (newStates) => {
        setStates((states) => Object.assign({}, states, newStates));
    };
    // 
    const toAwardList = () => {
        // const url = 'http://test.news.ifeng.com/loc/redEnvelopesLottery/priceList';
        // const url = `${baseUrl}/loc/redEnvelopesLottery/priceList`;
        const url = baseUrl;

        // 
        window.location.href = url;

        if (window.grounds) {
            // ()
            // window.grounds.dispatch('web', `${url}?isFull=0${getSearchParam('test') === 'true' ? '&test=true' : ''}`);
            window.grounds.dispatch('web', `${url}?isFull=0&immersion=true`);
        }
    };

    return { states, changeStates, toAwardList };
};

export const Store = createContainer(useStore);

// 
const useModalStore = (
    initialState = {
        awards: false,
        rules: false,
        signs: false,
    },
) => {
    const [modalStates, setModalStates] = useState(initialState);

    const changeModalStates = (newModalStates) => {
        setModalStates(Object.assign({}, modalStates, newModalStates));
    };

    return { modalStates, changeModalStates };
};

export const ModalStore = createContainer(useModalStore);
