import React from 'react';
import { ModalStore } from '../store';
import Awards from './Awards';
import Rules from './Rules';
import Signs from './Signs';
import './index.css';

const Modal = () => {
    const modalStore = ModalStore.useContainer();
    const { modalStates } = modalStore;

    return (
        <>
            {modalStates.awards ? <Awards /> : null}
            {modalStates.rules ? <Rules /> : null}
            {modalStates.signs ? <Signs /> : null}
        </>
    );
};

export default Modal;
