import React, { useEffect } from 'react';
// import { QQShare } from 'ThreeTerminal_mobile';
import styles from './index.css';
import { ModalStore, Store } from '../store';
import { getMyInfo } from '../service/index';
// import Grounds from 'ThreeTerminal_base/grounds';
import { removeSearchParam } from '../utils/index';

const shareInfo = {
    url: removeSearchParam('from'),
    title: '',
    desc: '',
    img: 'https://x0.ifengimg.com/fe/custom/a623a29303913ac93db567477e95f37f.png',
};

const Banner = () => {
    const modalStore = ModalStore.useContainer();
    const { changeModalStates } = modalStore;

    const handleRuleBtnClick = () => {
        changeModalStates({ rules: true });
    };

    const store = Store.useContainer();
    const { states, changeStates } = store;
    const { isGrounds } = states;

    useEffect(() => {
        if (window.grounds) {
            window.grounds.maximization();

            const isGrounds = true;
            const isLogin = window.grounds.userIsLogin();

            changeStates({ isGrounds, isLogin });

            if (isLogin) {
                getMyInfo()
                    .then(res => {
                        const { code, msg, data } = res;

                        if (code !== 200) {
                            console.error(msg);
                        }

                        if (data) {
                            const { coin, hdrawtime, ndrawtime } = data;

                            changeStates({ coin, hdrawtime, ndrawtime });
                        }
                    })
                    .catch(e => {
                        console.error(e);
                    });
            }
        }
    }, []);
    // 
    // useEffect(() => {
    //     if (window.localStorage) {
    //         const hasReadRules = localStorage.getItem('hasReadRules');

    //         if (hasReadRules !== '1') {
    //             changeModalStates({ rules: true });
    //             localStorage.setItem('hasReadRules', '1');
    //         }
    //     }
    // }, []);

    const handleBackClock = () => {
        window.grounds.close();
    };

    const handleShareClick = () => {
        // Grounds.actionStat({ type: 'jbzp_fx' });
        try {
            window.grounds.openShare(shareInfo.url, shareInfo.title, shareInfo.desc, shareInfo.img, 'id', '');
        } catch (e) {
            console.error(e);
            window.grounds.openShare(shareInfo.url, shareInfo.title, shareInfo.desc, shareInfo.img, 'id');
        }
    };

    return (
        <div className={`${styles.wrap} ${isGrounds ? styles.wrapGrounds : ''}`}>
            {/* <div className={styles.bannerText}>2021.11.03-2021.12.31</div> */}
            <div className={styles.ruleBtn} onClick={handleRuleBtnClick}>
                {/* <div className={styles.ruleBtnText}></div> */}
            </div>
            {isGrounds ? (
                <div className={styles.nav}>
                    <div className={styles.closeBtn} onClick={handleBackClock} />
                    {/* <div className={styles.shareBtn} onClick={handleShareClick} /> */}
                </div>
            ) : (
                // <QQShare
                //     content={{
                //         title: shareInfo.title,
                //         img: shareInfo.img,
                //         desc: shareInfo.desc,
                //         cb: () => {
                //             console.log('');
                //         },
                //     }}
                // />
                null
            )}
        </div>
    );
};

export default Banner;
