import React, { useEffect, useRef } from 'react';
import styles from './index.css';
import Game from './Game';
import { Store, ModalStore } from '../../store';
import Toast from '../../Toast';
import { getLotteryList, drawLottery } from '../../service/index';
import { callApp } from '@ifeng/callapp';
// import Grounds from 'ThreeTerminal_base/grounds';

// 3
const nextAction = [
    { x: 1, y: 0 },
    { x: 0, y: 1 },
    { x: -1, y: 0 },
    { x: 0, y: -1 },
];

const NORMAL_COST = [1000, 2000, 3000, 5000];
const SENIOR_COST = [10000, 20000, 30000, 50000];

const getSingleCost = (type, time) => {
    return type === 'normal' ? NORMAL_COST[time] : SENIOR_COST[time];
};

// 
const getDocType = () => {
    const ua = navigator.userAgent;

    return ua.indexOf('Mac OS X') > -1 ? 'wkweb' : 'web';
};

const Box = () => {
    // store
    const store = Store.useContainer();
    const { states, changeStates } = store;
    const {
        lotteryType,
        isPlaying,
        removeList,
        current,
        ndrawtime,
        hdrawtime,
        drawtime,
        isGrounds,
        isLogin,
        version,
        coin,
        normalList,
        seniorList,
    } = states;
    const modalStore = ModalStore.useContainer();
    const { changeModalStates } = modalStore;
    // toast
    const noAwardToast = useRef('');
    // 
    const gameRef = useRef(null);
    
    // UI
    useEffect(() => {
        getLotteryList()
            .then(res => {
                const { code, msg, data } = res;

                if (code !== 200) {
                    console.error(msg);
                }
                console.log('data',data);
                const { prize, version } = data;
        
                if (
                    prize &&
                    Array.isArray(prize) &&
                    prize.length
                ) {
                    changeStates({ normalList: prize, version });
                }
            })
            .catch(e => {
                console.error(e);
            });
    }, []);

    // 
    const awardList = lotteryType === 'normal' ? normalList : seniorList;

    // 12
    // if (awardList.length !== 12) {
    //     return null;
    // }

    // 
    const sideLength = awardList.length / 4 + 1;
    // 
    const itemSize = 100 / sideLength;
    // 
    const groupLength = sideLength - 1;
    // 
    let x = 0;
    let y = 0;
    // 
    const btnStyle = {
        width: `${itemSize * (sideLength - 2)}%`,
        height: `${itemSize * (sideLength - 2)}%`,
        left: `${itemSize}%`,
        top: `${itemSize}%`,
    };
    // toast
    const getNoAwardToast = () => {
        const failMessageList = ['~', '', ''];
        const filterFailMessageList = failMessageList.filter(item => item !== noAwardToast.current);
        const index = Math.floor(Math.random() * filterFailMessageList.length);
        const failMessage = filterFailMessageList[index];

        noAwardToast.current = failMessage;

        return failMessage;
    };
    // 
    const singleCost = getSingleCost(lotteryType, removeList.length);

    // 
    const drawAction = async () => {

        const reqData = {
            version, // 
            type: lotteryType === 'normal' ? 1 : 2,
        };

        if (removeList.length) {
            reqData.pass = removeList.join(',');
        }

        // const res = await drawLottery(reqData);

        const res = {
            "code": 200,
            "msg": "OK",
            "data": {
                "prizeid": 3,//ID
                "prizename": "",//
                "img": "https://x0.ifengimg.com/ucms/2022_03/2221198DF817023BBEB9D4D29E8E9857D433B0C4_size206_w957_h1278.png",
                "drawtime":4//
            }
        }
        
        const { code, msg, data } = res;

        if (code !== 200) {
            throw msg;
        }
                
        return data;
    };

    // 
    const callappAction = () => {
        callApp({
            docType: getDocType(),
            kind: 'inter_jbdzp01',
            pid: window.location.href,
            noKindFrom: 1,
            download: 'http://a.app.qq.com/o/simple.jsp?pkgname=com.ifeng.news2&ckey=CK1505157837361',
            downloadAd: 'https://statistics.appstore.ifeng.com/index.php/api/godownload?app_id=4&d_ch=8883',
        });
    };

    // YYYYMMDD
    const dateFormat = () => {
        const nowDate = new Date();
        const year = nowDate.getFullYear();
        let month = nowDate.getMonth() + 1;
        let day = nowDate.getDate();

        if (month < 10) month = `0${ month}`;
        if (day < 10) day = `0${ day}`;
        
        return year + month + day;
    };

    // 
    const handleBtnClick = async () => {
        // 
        // if (!isGrounds) {
        //     callappAction();

        //     return;
        // }

        // // 
        // if (!isLogin) {
        //     window.grounds.userLogin();

        //     return;
        // }

        // 
        if (isPlaying) {
            return;
        }

        // 
        const today = dateFormat();

        // 
        // if (drawtime <= 0) {
        //     // 
        //     if (today < '20220130') {
       
        //         Toast.default({ content: ' ' 
                
        //     });

        //         return;
        //     } else {
        //         // 
        //         Toast.default({ content: '' });

        //         return;
        //     }
        // }

        // if (singleCost > coin) {
        //     Toast.default({ content: '' });

        //     return;
        // }

        // 
        // Grounds.actionStat({ type: 'hongbaocj' });

        // 
        changeStates({ isPlaying: true });

        // 
        let data = null;

        try {
            data = await Promise.race([
                drawAction(), // 
                new Promise((resolve, reject) => {
                    setTimeout(() => reject(new Error('')), 5000);
                }),
            ]);

     
        } catch (e) {
            // toast
            Toast.default({ content: e });
            // 
            changeStates({ isPlaying: false });

            return;
        }

        if (data) {

            const { prizeid, prizename, img, coin, drawtime, lottret, iscoin } = data;
            // 
            // const drawtimeObj = lotteryType === 'normal' ? { ndrawtime: drawtime } : { hdrawtime: drawtime };
            const drawtimeObj = { drawtime };

            const finishIndex = awardList.findIndex(item => item.id === prizeid);

            // id
            if (finishIndex === -1) {
                Toast.default({ content: '' });

                return;
            }

            // 
            gameRef.current = new Game(current, awardList.length);
            gameRef.current.run(index => {
                changeStates({ current: index });
            });
            
            setTimeout(() => {
                // if (lottret === 2) { // 
                //     gameRef.current.finish(finishIndex, speed => {
                //         // 
                //         setTimeout(() => {
                //             changeStates({ award: { prizeid, prizename, img, iscoin }, coin, ...drawtimeObj });
                //             // 
                //             changeModalStates({ awards: true, rules: false });
                //         }, speed + 500);
                //     });
                // } else { // 
                //     gameRef.current.finish(finishIndex, speed => {
                //         // toast
                //         Toast.default({ content: getNoAwardToast() });
                //         setTimeout(() => {
                //             changeStates({ isPlaying: false, award: null, current: -1, coin, ...drawtimeObj });
                //         }, speed + 2500);
                //     });
                // }

                // 
                gameRef.current.finish(finishIndex, speed => {
                    // 
                    setTimeout(() => {
                        changeStates({ award: { prizeid, prizename, img, iscoin }, coin, ...drawtimeObj });
                        // 
                        changeModalStates({ awards: true, rules: false });
                    }, speed + 500);
                });
            }, 3000);
        }
    };

    // 
    const handleItemClick = id => {
        // 
        if (!isGrounds) {
            // callappAction();

            return;
        }

        // 
        if (!isLogin) {
            window.grounds.userLogin();

            return;
        }

        if (isPlaying) {
            return;
        }

        const index = removeList.findIndex(item => item === id);

        if (index === -1) {
            if (removeList.length >= 3) {
                // toast
                Toast.default({ content: '3' });

                return;
            } else {
                const list = [...removeList];

                list.push(id);
                changeStates({ removeList: list });
            }
        } else {
            const list = [...removeList];

            list.splice(index, 1);
            changeStates({ removeList: list });
        }
    };
    // 
    const getDrawtime = () => {
        if (isGrounds) {
            // return lotteryType === 'normal' ? ndrawtime : hdrawtime;
            return drawtime; 
        } else {
            return 0; // 
        }
    };

    return (
        <div className={`${styles.wrap} ${styles[`${lotteryType}Wrap`]}`}>
            {awardList.map((item, index) => {
                const { id, name, img } = item;
                const left = x * itemSize;
                const top = y * itemSize;
                const groupNum = Math.floor(index / groupLength);
                const action = nextAction[groupNum];
                const itemStyle = {
                    width: `${itemSize}%`,
                    height: `${itemSize}%`,
                    left: `${left}%`,
                    top: `${top}%`,
                };

                x += action.x;
                y += action.y;

                return (
                    <div
                        className={`${styles.item} ${current === index ? styles.itemCurrent : ''}`}
                        style={itemStyle}
                        key={id}
                        onClick={() => {
                            // handleItemClick(id);//
                        }}>
                        <div className={styles.itemContent}>
                            <div className={styles.itemImg}>
                                <img src={img} alt="" />
                            </div>
                            <div className={styles.itemText}>{name}</div>
                            {removeList.find(item => item === id) ? <div className={styles.itemMask} /> : null}
                        </div>
                    </div>
                );
            })}

            <div className={styles.buttonGrid} style={btnStyle} onClick={handleBtnClick}>
                <div className={`${styles.button} ${isPlaying ? styles.buttonPlaying : ''}`}>
                    <div className={styles.buttonText}>{isLogin ? `${getDrawtime()}` : ''}</div>
                </div>
                {/* <div className={styles.buttonGridText}>{singleCost}</div> */}
            </div>
        </div>
    );
};

export default Box;
