const getBaseUrl = (env) => {
    switch (env) {
        case 'development':
            return apiUrl;
        case 'staging':
            return apiUrl;
        case 'production':
            return apiUrl;
        default:
            break;
    }
};

// console.log('baseUrl',getBaseUrl(NODE_ENV));

// webpack
const baseUrl = getBaseUrl(NODE_ENV);

// webpage
// console.log(NODE_ENV);

// 
/* eslint-disable no-undef */
const isDev = NODE_ENV === 'development';

// 
const isGrounds = isDev ? true : !!window.grounds;
/**
 * @description 
 * @returns {string} token
 */
const getToken = () => {
    const defaultToken = isDev ? '58E45BCC46F50D2FCFE18D799951B1FA' : '';

    return window?.grounds?.getUserInfo()?.getToken() || defaultToken;
};

const request = async ({ url, params, name }) => {
    if (window.grounds) {
        // Grounds.nativeRequest
        return Grounds.nativeRequest({
            url,
            getParams: JSON.stringify(params),
            type: 'get',
        });
    } else {
        // jsonp
        return jsonp(url, {
            data: params,
            jsonpCallback: name,
            cache: false,
        });
    }
};

