import React, { useState, useEffect } from 'react';
import styles from './index.css';
import { ModalStore, Store } from '../store';
// server
import { signInLottery } from '../service';
// 
// import Grounds from 'ThreeTerminal_base/grounds';
// 
import Toast from '../Toast';
// classNames
import classNames from 'classnames/bind';
const cx = classNames.bind(styles);

const SignIn = () => {
    const store = Store.useContainer();
    const { states, changeStates } = store;
    const { signlist, signdays, isGrounds, isLogin } = states;
    // 
    const modalStore = ModalStore.useContainer();
    const { changeModalStates } = modalStore;

    // YYYYMMDD
    const dateFormat = () => {
        const nowDate = new Date();
        const year = nowDate.getFullYear();
        let month = nowDate.getMonth() + 1;
        let day = nowDate.getDate();

        if (month < 10) month = `0${ month}`;
        if (day < 10) day = `0${ day}`;
        
        return year + month + day;
    };
    // YYYYMMDD => Date
    const dateStringToDate = (dateString) => {
        try {
          const year = dateString.substring(0, 4);
          const month = dateString.substring(4, 6);
          const day = dateString.substring(6, 8);
          let date = new Date(year, month - 1, day);
          const offset = date.getTimezoneOffset();

          date = new Date(date.getTime() - (offset * 60 * 1000));
                
            return date;
        } catch (error) {
            return null;
        }
      };
    // 
    const getToday = (date) => {
        // 
        // const todayTimeStamp = (new Date(new Date().setHours(0, 0, 0, 0)) / 1000).toString();   
        // 
        const today = dateFormat(); // YYYYMMDD

        if (today === date) { 
            // console.log('');
            return true; 
        } else {
            // console.log('');
            return false; 
        }

    };

    // 
    const getState = (timeStampStr, signIn) => {
        // 
        // const todayTimeStamp = (new Date(new Date().setHours(0, 0, 0, 0)) / 1000).toString();   
        // 
        const todayStr = dateFormat(); // YYYYMMDD 
        // 0: / 1: / 2:
        let state = ''; 

        // console.log(timeStampStr === todayStr, timeStampStr, todayStr, signIn);

        if (signIn === '1') {
            return state = '';
        } else if (timeStampStr < todayStr) {
            return state = '';
        } else {
            return state = '';
        }
    
    };

    // 
    const objToArr = (obj) => {

        const arr = [];

        // 
        if (window.grounds) {
            const isLogin = window.grounds.userIsLogin();

            if (isLogin) {
                for (const i in obj) {
                    const newObj = {
                        'timeStamp': i, // 
                        'signIn': obj[i], // 0  / 1 
                        'state': getState(i, obj[i]), // 0  / 1 / 2 
                        'isToday': getToday(i)
                    };
                    
                    // newObj[i] = obj[i];
                    arr.push(newObj);
                }
            } else {
                for (const i in obj) {
                    const newObj = {
                        'timeStamp': i, // 
                        'signIn': obj[i], // 0  / 1 
                        'state': '', // 0  / 1 / 2 
                        'isToday': getToday(i)
                    };
                    
                    // newObj[i] = obj[i];
                    arr.push(newObj);
                }
            }
        } else {
            for (const i in obj) {
                const newObj = {
                    'timeStamp': i, // 
                    'signIn': obj[i], // 0  / 1 
                    'state': '', // 0  / 1 / 2 
                    'isToday': getToday(i)
                };
                
                // newObj[i] = obj[i];
                arr.push(newObj);
            }

        }
        
return arr;
    };

    // UI
    useEffect(() => {
        if (window.grounds) {
            window.grounds.maximization();

            const isGrounds = true;
            const isLogin = window.grounds.userIsLogin();
    
            if (isLogin) {
                signInLottery()
                .then(res => {
    
                    const { code, msg, data } = res;
    
                    console.log('', res);
                    if (code !== 200) {
                        console.error(msg);
                    }
          
                    const { 
                        drawtime, // 
                        signlist, // 
                        adddrawtime, // 
                        signdays, // 
                        signret // 1 2
                    } = data;
    
                    // 
                    if (window.grounds) {
                        const isLogin = window.grounds.userIsLogin();
                        
                        if (signret === 1 && isLogin) {
                            Toast.default({ content: `+${adddrawtime}` });
                            console.log('');
                        } else {
                            console.log('');
                        }
                    }
    
                    // 
                    if (signlist) {
                        changeStates({ drawtime, signlist: objToArr(signlist), signdays });
                    }
    
                })
                .catch(e => {
                    console.error(e);
                });
            } else { // 
                    const defualt = {
                        '20290111': '0',
                        '20290112': '0',
                        '20290113': '0',
                        '20290114': '0',
                        '20290117': '0',
                        '20290118': '0',
                        '20290119': '0'
                    };
                
                    // 
                     changeStates({ drawtime: 0, signlist: objToArr(defualt), signdays: 2 });
              
            }
        } else { // 
            const defualt = {
                '20290111': '0',
                '20290112': '0',
                '20290113': '0',
                '20290114': '0',
                '20290117': '0',
                '20290118': '0',
                '20290119': '0'
            };
        
            // 
             changeStates({ drawtime: 0, signlist: objToArr(defualt), signdays: 2 });
        }

    }, []);

    const handleRuleBtnClick = () => {
        changeModalStates({ signs: true });
    };
    
return (
        <div className={styles.wrap}>
            <div className={styles.content}>

                <div className={styles.header}>
                    <div className={styles.sign}><span className={styles.day}>{!isLogin ? 0 : signdays}</span></div> 
                    <div className={styles.rule} onClick={handleRuleBtnClick}>
                            <div className={styles.ruleText}></div>
                            <div className={styles.ruleIcon} />
                    </div>
                </div>
                
            <div className={styles.listWrap}>
            <div className={styles.list}>
                {signlist.map((item, index) => {
                  return (
                      <div
                          className={styles.itemWrap}
                          key={index}>

                              <div className={styles.cardWrap}>
                                    <div
                                        className={cx({
                                            itemIcon: true,
                                            signIcon: item.state === '', /*  */
                                            noSignIcon: item.state === '', /*  */
                                            noDateIcon: item.state === ''/*  */
                                    })} 
                                    />

                                    <div
                                    
                                        className={cx({
                                            textList: true,
                                            signText: item.state === '', /*  */
                                            noSignText: item.state === '', /*  */
                                            noDateText: item.state === ''/*  */
                                    })} >
                                        {item.isToday ? '' : `${index + 1}`}
                                    </div>
                              </div>

                            <div
                                className={cx({
                                    itemBg: true,
                                    signedBg: item.state === '', /*  */
                                    noSignBg: item.state === '', /*  */
                                    noDateBg: item.state === ''/*  */
                            })} />

                      </div>
                  );
                })}
            </div> 
            </div>
            </div>

        </div>
    );
};

export default SignIn;
