import React from 'react';
import { Store } from '../store';
import styles from './index.css';
// 
import { callApp } from '@ifeng/callapp';
// 
// import Grounds from 'ThreeTerminal_base/grounds';

const Bottom = () => {
    const {
        states: { coin, isGrounds, isLogin },
        toAwardList,
    } = Store.useContainer();

    // 
    const getDocType = () => {
        const ua = navigator.userAgent;
    
        return ua.indexOf('Mac OS X') > -1 ? 'wkweb' : 'web';
    };

    // 
    const callappAction = () => {
        callApp({
            docType: getDocType(),
            kind: 'inter_jbdzp01',
            pid: window.location.href,
            noKindFrom: 1,
            download: 'http://a.app.qq.com/o/simple.jsp?pkgname=com.ifeng.news2&ckey=CK1505157837361',
            downloadAd: 'https://statistics.appstore.ifeng.com/index.php/api/godownload?app_id=4&d_ch=8883',
        });
    };
    const handleCheckRecord = () => {
        if (isGrounds && isLogin) {
            // 
            toAwardList();
        }
    };

    const handleCheckLogin = () => {
        // 
        if (!isGrounds) {
            callappAction();

            return;
        }

        // 
        if (!isLogin) {
            window.grounds.userLogin();
            
    return;
        }
    };

    return (
        <>
            {isGrounds && isLogin ? (
                <div className={styles.wrap}>
                    <div className={styles.content}>
                    <div className={styles.myRecordButton} onClick={handleCheckRecord} >
                        <span className={styles.text}></span>    
                    </div>
                    </div>
                </div>
            ) : 
            <div className={styles.wrap}>
                <div className={styles.content}>
                    <div className={styles.myRecordButton} onClick={handleCheckLogin} >
                        
                        <span className={styles.text}></span> 

                    </div>
                </div>
            </div>
            }
        </>
    );
};

export default Bottom;
