import React, { useRef } from 'react';
import ReactDOM from 'react-dom';
import ModalToast from './component';

const show_toast = props => {
    let component = null;
    const lastEle = document.getElementById('toastModal');

    if (lastEle) {
        lastEle.parentElement.removeChild(lastEle); // 
    }
    const div = document.createElement('div');

    div.setAttribute('id', 'toastModal');
    document.body.appendChild(div);

    const onClose = () => {
        if (div) {
            ReactDOM.unmountComponentAtNode(div);
            if (div.parentElement) div.parentElement.removeChild(div);
        }

        if (typeof props.onClose === 'function') {
            props.onClose();
        }
    };

    // 
    const replaceStr = (str) => {

        if (str.indexOf(' ') >= 0) {

            const strArr = str.split(' ');
            const arr = [];

            for (let i = 0; i < strArr.length; i++) {
                arr.push(`<div>${strArr[i]}</div>`);
            }
            const arrToStr = arr.toString().replace(/,/g, '');
            
            // eslint-disable-next-line react/jsx-filename-extension
            return <div dangerouslySetInnerHTML={{ __html: arrToStr }} />;
        } else {
            return str;
        }

    };

    ReactDOM.render(
        /* eslint-disable */
        <ModalToast {...props} onClose={onClose} ref={c => (component = c)} isOpen >
            {/* { props.content} */}
            { replaceStr(props.content) }
            
        </ModalToast>,
        div,
    );
};

const Toast = {};

Toast.success = props =>
    show_toast({
        ...props,
        type: 'success',
    });
Toast.warning = props =>
    show_toast({
        ...props,
        type: 'warning',
    });

Toast.default = props =>
    show_toast({
        ...props,
        type: 'default',
    });

export default Toast;
