import React from 'react';
import PropTypes from 'prop-types';
import styles from './index.css';
import { Store } from '../store';

const Wrap = ({ children }) => {
    const store = Store.useContainer();
    const { states } = store;
    const { isGrounds } = states;

    return <div className={`${styles.wrap} ${isGrounds ? styles.wrapGrounds : ''}`}>{children}</div>;
};

Wrap.propTypes = {
    children: PropTypes.array,
};

export default Wrap;
