import React from 'react';
import styles from './index.css';
import Box from './Box';
import { Store } from '../store';

const Lottery = () => {
    const store = Store.useContainer();
    const { states, changeStates } = store;
    const { lotteryType, isPlaying } = states;

    const handleTabClick = type => {
        if (type !== lotteryType && !isPlaying) {
            // 
            changeStates({ lotteryType: type, removeList: [], current: -1 });
        }
    };

    return (
        <div className={`${styles.wrap} ${styles[`${lotteryType}Wrap`]}`}>
            <div className={styles.tabWrap}>
                <div
                    className={styles.tab}
                    onClick={() => {
                        handleTabClick('normal');
                    }}
                />
                {/* <div
                    className={styles.tab}
                    onClick={() => {
                        handleTabClick('senior');
                    }}
                /> */}
            </div>
            <Box />
            {/* <div className={styles.text}> 3</div> */}
        </div>
    );
};

export default Lottery;
