// 
const MAX_SPEED = 50;
// 
const MIN_SPEED = 300;

class Game {
    constructor(index, size) {
        // 
        this.index = index;
        // 
        this.size = size;
        // 
        this.speed = MIN_SPEED;
        // 
        this.accelerated = true;
        // 
        this.isFinish = false;
        // 
        this.endIndex = -1;
        // 
        this.finishCallback = () => {};
        // 
        this.error = false;
    }

    // 
    speedUp() {
        this.speed -= 30;
    }

    // 
    speedDown() {
        this.speed += 80;
    }

    // 
    speedControl() {
        // 
        if (this.error) {
            this.isFinish = true;
            if (this.finishCallback && typeof this.finishCallback === 'function') {
                this.finishCallback();
            }

            return;
        }

        // 
        if (this.accelerated) {
            if (this.speed > MAX_SPEED) {
                this.speedUp();
            }
        }

        // 
        if (!this.accelerated) {
            if (this.speed < MIN_SPEED) {
                this.speedDown();
            }
            if (this.endIndex === this.index) {
                this.isFinish = true;
                if (this.finishCallback && typeof this.finishCallback === 'function') {
                    this.finishCallback(this.speed);
                }
            }
        }
    }

    finish(index, finishCallback, error = false) {
        this.accelerated = false;
        this.endIndex = index;
        this.finishCallback = finishCallback;
        this.error = error;
    }

    addIndex() {
        this.index = (this.index + 1) % this.size;
    }

    run(cb) {
        this.addIndex();

        if (cb && typeof cb === 'function') {
            cb(this.index);
        }
        this.speedControl();
        setTimeout(() => {
            if (!this.isFinish) {
                this.run(cb);
            }
        }, this.speed);
    }
}

export default Game;
